@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
.about{background:#2b2b2b;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.about__wapper{padding:0 10%;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.about__picture{width:25%;-webkit-flex-shrink:0;flex-shrink:0;text-align:left}.about__picture>img{width:120px;height:120px}.about__info{text-align:left}.info__tab>h2{font:bold 22px/30px "Open Sans", sans-serif;color:#fff;margin-bottom:12px}.info__tab>p{line-height:30px;color:#7a7a7a}.info__tab>p>span{display:block}.info__tab>p>span svg{width:20px !important;color:white;margin-right:0.5em}

.banner{background-image:url(/static/media/bg.ba105921.jpg);color:white;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;background-color:#3f5e8c}.banner__wapper{width:85%;text-align:center;padding-bottom:30px}.banner__title{font:bold 90px/1.1em 'Open Sans', sans-serif;color:#fff;letter-spacing:-2px;margin:0 auto 18px auto;text-shadow:0px 1px 3px rgba(0,0,0,0.8)}.banner__desc{font-size:16px;color:#A8A8A8;margin:0 auto;width:70%;text-shadow:0px 1px 2px rgba(0,0,0,0.5)}.banner__desc span{color:white}.banner__social{margin:24px 0;padding:0;font-size:30px;text-shadow:0px 1px 2px rgba(0,0,0,0.8)}.banner__social a{display:inline-block;margin:0 15px;padding:0;color:white;transition:500ms -webkit-transform;transition:500ms transform;transition:500ms transform, 500ms -webkit-transform}.banner__social a:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}

.contact{background:#191919;padding-top:96px;padding-bottom:102px;color:#636363;text-align:left}.contact .contact__head{margin-bottom:25px}.contact input,.contact textarea,.contact select{padding:10px 20px;color:#eee;background:#373233;margin-bottom:20px;border:0;outline:none;font-size:15px;line-height:24px;width:65%;resize:none}.contact label{font:bold 15px/24px "Open Sans", sans-serif;float:left;margin:10px 0;color:#ebeeee;display:inline-block;width:26%}.contact button{text-transform:uppercase;letter-spacing:3px;color:#fff;background:#0D0D0D;border:none;border-radius:3px;margin-left:26%;padding:10px 15px}.draw-border{box-shadow:inset 0 0 0 2px #0D0D0D;color:#0D0D0D;transition:color .25s .08333s;position:relative}.draw-border::before,.draw-border::after{border:0 solid transparent;box-sizing:border-box;content:'';pointer-events:none;position:absolute;width:0;height:0;bottom:0;right:0}.draw-border::before{border-bottom-width:2px;border-left-width:2px}.draw-border::after{border-top-width:2px;border-right-width:2px}.draw-border:hover{color:#fff}.draw-border:hover::before,.draw-border:hover::after{border-color:#fff;transition:border-color 0s,width .25s,height .25s;width:100%;height:100%}.draw-border:hover::before{transition-delay:0s,0s,.25s}.draw-border:hover::after{transition-delay:0s,.25s,0s}.btn{background:none;border:none;cursor:pointer;line-height:1.5;font:700 1.2rem 'Roboto Slab', sans-serif;padding:1em 2em;letter-spacing:0.05rem}.btn:focus{outline:2px dotted #55d7dc}

.home>*{height:100vh;min-height:700px}

.works{padding-top:90px;padding-bottom:72px}.works h1{font:bold 18px/24px "Open Sans", sans-serif;text-transform:uppercase;letter-spacing:1px}.works h3{font:bold 25px/30px "Open Sans", sans-serif}.resume__item{text-align:left}.resume__item__title h1{border-bottom:3px solid #11abb0;padding-bottom:6px;display:inline}.resume__item__body>div .item__info{font:16px/24px;color:#6e7881;margin-bottom:18px;margin-top:9px}.resume__item__body>div .item__info span:first-child{font-style:italic;padding-right:10px}.resume__item__body>div .item__info span:first-child:after{content:"•";margin-left:10px}.resume__item__body>div p{font:15px "Open Sans";margin:0 0 30px 0;color:#838c95}

.resume{padding-top:90px;padding-bottom:72px}.resume h1{font:bold 18px/24px "Open Sans", sans-serif;text-transform:uppercase;letter-spacing:1px}.resume h3{font:bold 25px/30px "Open Sans", sans-serif}.resume__item{text-align:left}.resume__item__title h1{border-bottom:3px solid #11abb0;padding-bottom:6px;display:inline}.resume__item__body>div .item__info{font:16px/24px;color:#6e7881;margin-bottom:18px;margin-top:9px}.resume__item__body>div .item__info span:first-child{font-style:italic;padding-right:10px}.resume__item__body>div .item__info span:first-child:after{content:"•";margin-left:10px}.resume__item__body>div p{font:15px "Open Sans";margin:0 0 30px 0;color:#838c95}.skill{margin-top:36px;list-style:none;padding:0}.skill__item{position:relative;margin-bottom:40px;background:#ccc;height:20px;border-radius:3px}.skill__item em{font:15px "opensans-bold", sans-serif;color:#313131;text-transform:uppercase;letter-spacing:2px;font-weight:normal;position:relative;top:-26px}.skill__item span{height:100%;display:block;position:absolute;top:0;border-radius:3px;background-color:#313131;transition:width 1s}.aos-init .skill__item span{width:0}.aos-init.aos-animate .skill__item .level-1{width:20%}.aos-init.aos-animate .skill__item .level-2{width:40%}.aos-init.aos-animate .skill__item .level-3{width:60%}.aos-init.aos-animate .skill__item .level-4{width:80%}.aos-init.aos-animate .skill__item .level-5{width:100%}

